import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import Select from 'react-select'
import { GET_UGC_POST_DETAIL, ugcModal } from '../../../../../../actions/moderationActions'
import { Container, Tooltip, OverlayTrigger, Badge, Button, Form } from 'react-bootstrap'
import ModerationPagination from '../../Elements/Pagination'
import ErrorModal from '../../UgcRights/Elements/ErrorModal'
import PermissionError from '../../UgcRights/Elements/PermissionError'
import { FontAwesomeIcon } from '../../../../../CommonComponents/AssetsHelper' 
import AddTag from '../../Elements/AddTag'


const ModSubHeader = (props) => {
    const { moderation, activeTab } = props
    const [addStatus, setAddTag] = useState(false)
    const categories = [{ label: "50", value: 50 }, { label: "100", value: 100 }, { label: "200", value: 200 }]
    const [selectedEntry, selectedEntryFun] = useState([{ label: String(moderation?.filter?.limit), value: parseInt(moderation?.filter?.limit) }])
    const [permissionError, setPermissionError] = useState(false)
    const [ugcError, setUgcError] = useState(false)
    const [showGetRight, setShowGetRight] = useState(false)
    const [fromSnapup, setFromSnapup] = useState(false)
    const [refresh, setRefresh] = useState(false)
    const dispatch = useDispatch()

    const UserRuleManualTag = "";
    const UserRuleEnableAI = "";

    const updateLimit = value => { 
        props.limitUpdate(parseInt(value))
        selectedEntryFun([{ label: String(value), value: parseInt(value) }])
    }
    const refreshPost = () => { props.clickToRefresh(); setTimeout(() => { setRefresh(false) }, 2000) }

    const addTag = () => {
        setAddTag(true)
    }

    const clickToCloseAddTag = event => {
        setAddTag(false)
    }
    useEffect(() => {
        checkGetRight()
    }, [moderation])


    const getRights = () => {
        const { check_uncheck_all } = moderation
        let userId = null
        var flag = false
        check_uncheck_all.data.map(post => {
            if (post.ugcstatus == 1 && !flag) {
                setPermissionError(true)
                flag = true
            }
            if (post.networkId == 29) {
                setPermissionError(true)
                setFromSnapup(true)
                flag = true
            }
            if (post.type == 1) {
                setUgcError(true)
                flag = true
            }
            if (!((post.networkId == 3 && post.feedFilter == 62) || (post.networkId == 18 && (post.feedFilter == 24 || post.feedFilter == 25 || post.feedFilter == 26)))) {
                setPermissionError(true)
                setFromSnapup(true)
                flag = true
            }

            if (userId == null) userId = post.username
            else if (userId != null && userId != post.username && !flag) {
                setUgcError(true)
                flag = true
            }
        })
        if (!flag) {
            const id = check_uncheck_all.data.map(item => item.id)
            dispatch(GET_UGC_POST_DETAIL({ is_carausel: 0, postId: id, id, }))
            dispatch(ugcModal(true))
        }
    }

    const checkGetRight = () => {
        const { check_uncheck_all } = moderation
        var flag = false

        check_uncheck_all.data.map(post => {
            if (post.ugcstatus == 1 && !flag) flag = true
            if (post.networkId == 29) flag = true
            if (post.type == 1) flag = true
            if (!((post.networkId == 3 && post.feedFilter == 62) || (post.networkId == 18 && (post.feedFilter == 24 || post.feedFilter == 25 || post.feedFilter == 26)))) flag = true
        })
        setShowGetRight(!flag)
    }

    return <Container fluid>
            <div className="f-between py-2 mod_header">
                <div className="d-flex align-items-center">
                    {/* <Form.Check type="checkbox" id="select_all_" className='form-check btn btn-icon btn-sm d-flex align-items-center me-2 border-0 position-relative' onChange={props.clickToCheckAll(moderation.check_uncheck_all.status)} checked={moderation.check_uncheck_all.status}/> */}
                    
                    <Form.Check type="checkbox" id="select_all_" className='btn btn-outline-secondary h-32px p-2 me-2 text-nowrap my-1 d-flex align-items-center' label={`${moderation.check_uncheck_all.data.length ? moderation.check_uncheck_all.data.length +` Selected` : `Select All`}`} onChange={props.clickToCheckAll(moderation.check_uncheck_all.status)} checked={moderation.check_uncheck_all.status}/>
                    <OverlayTrigger placement="top" overlay={<Tooltip>Refresh</Tooltip>}>
                        <Button variant='outline-secondary' size='sm' className="me-2 p-0 h-32px w-32px btn-icon" onClick={() => { setRefresh(true); refreshPost()}}>
                            <span className={`d-inline-block ${refresh ? 'rotate-360' : ''}`}><FontAwesomeIcon icon={'arrows-rotate'} iconClass={'fs-6'} /></span>
                        </Button>
                    </OverlayTrigger>
                    {moderation.check_uncheck_all && moderation.check_uncheck_all.data.length > 0 ? 
                    <div className="d-flex align-items-center mod_actions flex-wrap">
                        {activeTab == 0 || activeTab == 2 ? <Button variant='success' size="sm" className="me-2 py-1 px-3 f-center h-32px" onClick={props.onClickToFilterUpdateStatus(1)}>
                            <FontAwesomeIcon icon={'check'} iconClass='me-2 p-0 f-center fs-6'/>
                            Public
                        </Button>: null}
                        {activeTab == 0 || activeTab == 1 ? <Button variant='danger' size="sm" className="me-2 py-1 px-3 f-center h-32px" onClick={props.onClickToFilterUpdateStatus(2)}>
                            <FontAwesomeIcon icon={'xmark'} iconClass='me-2 p-0 f-center fs-6'/>
                            Private
                        </Button>: null}
                        <Button variant='warning' size="sm" className="me-2 py-1 px-3 f-center h-32px" onClick={props.onClickToFilterDeleteAll}>
                            <FontAwesomeIcon icon={'trash-can'} iconClass='me-2 p-0 f-center fs-6'/>
                            Delete
                        </Button>
                        {!(UserRuleManualTag || UserRuleEnableAI) ? <Button variant='info' size="sm" className="me-2 py-1 px-3 f-center h-32px" onClick={() => addTag()}>
                            <FontAwesomeIcon type="solid" icon={'tag'} iconClass='me-2 p-0 f-center fs-6'/>
                            Add Tag
                        </Button>:null}
                        
                        {showGetRight ? <Button variant='dark' size="sm" className="me-2 py-1 px-3 f-center h-32px" onClick={() => getRights()}>
                            <FontAwesomeIcon type={'solid'} icon={'shield-check'} iconClass='me-2 p-0 f-center fs-6'/>
                            UGC Rights
                        </Button>:null}
                    </div>
                    
                    :null}
                </div>
                <div className="d-flex align-items-center">
                    {/* <span class="d-none d-sm-inline">Show</span> */}
                    <Select className={`selectpicker selectpicker-sm d-none d-lg-flex mx-2`} classNamePrefix="selectpicker" isSearchable={false} options={categories} value={selectedEntry} onChange={e => updateLimit(e?.value)} /> 
                    {/* <span class="d-none d-sm-inline">entries</span> */}
                    <ModerationPagination {...props} />
                </div>
            </div>
        <PermissionError show={permissionError} fromSnapup={fromSnapup} onHide={() => setPermissionError(false)} />
        <ErrorModal show={ugcError} onHide={() => setUgcError(false)} />
        {addStatus == true ? <AddTag tags={props.moderation.filter} {...props} onUpdateTags={props.onUpdateTags} onDeleteTags={props.onDeleteTags} data={props.moderation.listData} open={addStatus} clickToCloseAddTag={clickToCloseAddTag} /> : null}
    </Container>

}
export default ModSubHeader;