import React, { useEffect, useRef } from "react";
import moment from 'moment'
import DateRangePicker from 'react-bootstrap-daterangepicker';
import $ from 'jquery';
import 'bootstrap-daterangepicker/daterangepicker.css';

const DateRange = (props) => {
    const { children, maxDate, handleOnApply, startDate, endDate, show, onShow, onHide } = props;
    const pickerRef = useRef(null);

    useEffect(() => {
        if (show && pickerRef.current) {
            $(pickerRef.current)?.data('daterangepicker')?.show();
            if (onShow) {
                onShow();
            }
        } else if (!show && pickerRef.current) {
            $(pickerRef.current)?.data('daterangepicker')?.hide();
            if (onHide) {
                onHide();
            }
        }
    }, [show, onShow, onHide]);
    
    return <DateRangePicker 
        ref={pickerRef}
        onApply={handleOnApply}
        startDate={startDate} 
        endDate={endDate}
        initialSettings={{
            locale: { format: 'DD-MM-YYYY', separator: '-' },
            maxDate: maxDate,
            showDropdowns: true,
            startDate: startDate, 
            endDate: endDate,
            autoApply: false,
            ranges: {
                Today: [moment().format("DD-MM-YYYY"), moment().format("DD-MM-YYYY")],
                Yesterday: [
                    moment().subtract(1, 'days').format("DD-MM-YYYY"),
                    moment().subtract(1, 'days').format("DD-MM-YYYY"),
                ],
                'Last 7 Days': [
                    moment().subtract(6, 'days').format("DD-MM-YYYY"),
                    moment().format("DD-MM-YYYY"),
                ],
                'Last 30 Days': [
                    moment().subtract(29, 'days').format("DD-MM-YYYY"),
                    moment().format("DD-MM-YYYY"),
                ],
                'This Month': [
                    moment().startOf('month').format("DD-MM-YYYY"),
                    moment().endOf('month').format("DD-MM-YYYY"),
                ],
                'Last Month': [
                    moment().subtract(1, 'month').startOf('month').format("DD-MM-YYYY"),
                    moment().subtract(1, 'month').endOf('month').format("DD-MM-YYYY"),
                ],
            },
        }}
    >
        {children}
    </DateRangePicker>
}

export default DateRange