import React, { useState, useEffect } from 'react'
import { autoCompleteSearch } from '../../../../../../actions/moderationActions'
import { Button, InputGroup, Form, Dropdown } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../CommonComponents/AssetsHelper'

const QuickSearch = (props) => {
    const [tagSearchList, setTagSearchList] = useState([]);
    const [searchPost, setSearchPost] = useState("");
    const [selected, setSelected] = useState("");
    const [openSearch, setOpenSearch] = useState(false);
    const [prevProps, setPrevProps] = useState(props);

    const onLoad = () => {
        if (searchPost.length > 0) {
            autoCompleteSearch(searchPost).then((response) => {
                const { responseData, responseCode } = response.data;
                if (responseCode === 200) {
                    setOpenSearch(responseData?.tags?.length)
                    setTagSearchList(responseData?.tags);
                }
                else setTagSearchList([]);
            });
        } else {
            setTagSearchList([]);
        }
    };

    useEffect(() => {
        if (searchPost) {
            onLoad();
        }
    }, [searchPost]);

    const onClickToSearchTags = (selectSearch) => (event) => {
        setOpenSearch(false)
        setSelected(selectSearch);
        setTagSearchList([]);
        props.searchTagsFilter(selectSearch);
    };

    const onClickToSearchPost = (selectSearch) => (event) => {
        if (selectSearch && String(selectSearch).length > 0) {
            setSelected(selectSearch);
            setTagSearchList([]);
            setOpenSearch(false);
            props.searchPostFilter(selectSearch);
        }
    };

    useEffect(() => {
            setSearchPost(props.moderation?.filter?.text);
    }, [props.moderation?.filter?.text]);

    const { filterTab, changeFilterTab } = props;
    return (
        <>
            <Button variant='icon' size='sm' className="btn-active-primary btn-link border-0" onClick={() => changeFilterTab(filterTab == 1 ? 0 : 1)}>
                <FontAwesomeIcon icon={filterTab == 1 ? 'xmark' : 'magnifying-glass'} />
            </Button>
            <Dropdown show={tagSearchList && tagSearchList.length > 0} className="assets_search_input position-absolute me-lg-2" style={{ display: filterTab == 1 ? 'block' : 'none' }}>
                <div className="quick_search d-flex align-items-center">
                    <InputGroup id="inputSpinner" className="p-0 spinner-gray spinner-sm">
                        {filterTab == 1 && <Form.Control size='sm' style={{ minHeight: '36px' }} className='' id="assets_search_input" placeholder="What are you looking for?" value={searchPost} onChange={(e) => setSearchPost(e.target.value)} autoComplete='off' />}
                        <Button className='' size='sm' id="inputCloseButton" variant="primary" onClick={onClickToSearchPost(searchPost)}>
                            <FontAwesomeIcon icon="magnifying-glass" />
                        </Button>
                    </InputGroup>
                    {(tagSearchList && tagSearchList.length > 0 && openSearch) ? <Dropdown.Menu className="w-100 dropdwon-height top-100" style={{ overflowY: 'auto' }}>
                        {
                            tagSearchList.map((item) => <Dropdown.Item key={item} onClick={onClickToSearchTags(item)}>{item}</Dropdown.Item>)
                        }
                    </Dropdown.Menu> : null}
                </div>
            </Dropdown>
        </>
    );
};

export default QuickSearch;