import React, { Component } from 'react'
import { toast } from 'react-toastify';
import { Button, Modal } from "react-bootstrap";  
import {  checkIfStringHasSpecialChar, checkWordCount, capitalizeFirstLetter } from '../../../../../utils';
import { FontAwesomeIcon } from '../../../../CommonComponents/AssetsHelper';
import { autoCompleteSearch } from '../../../../../actions/moderationActions';
class AddTag extends Component {
    state = {
        searchData: [],
        searchPost: "",
        selected: "",
        submitTags: [],
        submitTagsNew: [],
        removeTag: [],
        isLoading: false,
        isRequest: false,
        isDeleteRequest: false,
        postTypes: "0",
        postTagAction: "2",
        totalCountTag: "0",
        tagRemoveAction: "0",
        addTags: false
    }

    componentDidMount() {
        const { tags } = this.props;
        let submitTags = [];
        if (tags && String(tags.tags).split(",").length) {

            String(tags.tags).split(",").map((iTag) => {
                if (iTag && iTag.length > 0) {
                    submitTags.push(String(iTag).toLocaleLowerCase())

                }
            })
            this.setState({ submitTags: submitTags })
        }
        this.onSearchText()
    }

    onClickToSave = event => {

        let { submitTags, tagRemoveAction,  removeTag, postTagAction, totalCountTag, addTags } = this.state;
        const { moderation,wallId} = this.props


        if (addTags && submitTags && submitTags.length > 0) {
            let postId = []
            let postTypes = []
            let feedIds = []
            if (moderation.check_uncheck_all && moderation.check_uncheck_all.data) {
                moderation.check_uncheck_all.data.map((item) => {
                    postId.push(item.id)
                    postTypes.push(item.type)
                    feedIds.push(item.feedId)
                })
            }
            var updateSubmitTags = [];
            if (submitTags && submitTags.length > 0) {
                submitTags.map((sTags) => updateSubmitTags.push(capitalizeFirstLetter(sTags)))
            }
            let saveObj = { 
                postId: postId.join(),
                feedId: feedIds.join(), 
                wallId: localStorage.getItem('wallId'),
                tags: updateSubmitTags.join(), 
            };
            this.props.onUpdateTags(saveObj, () => this.setState({ isRequest: true }), () => {
                this.setState({ isRequest: false, removeTag: [], submitTagsNew: [] })
                this.props.clickToCloseAddTag()
            });
        }
        else {

            let textInput = document.getElementById('searchTagpost').value;

            toast.error(textInput.trim().length != 0 && checkIfStringHasSpecialChar(textInput) ? 'Special character not allowed.' : checkWordCount(textInput) ? 'Please click add button then save.' : (String(textInput).length && textInput.trim().length != 0 ? 'Please enter minimum 3 characters.' : 'Please Enter tags.'));
        }
    }
    onClickToDelete = event => {
        let { submitTags } = this.state;
        const { moderation, wallId } = this.props
        if (submitTags && submitTags.length > 0) {
            var isExists = false


            let spostId = []
            let id = []
            let postTypes = []

            if (moderation.check_uncheck_all && moderation.check_uncheck_all.data) {

                submitTags.map((checkI) => {

                    if (!isExists && submitTags && submitTags.length > 0) {

                        moderation.check_uncheck_all.data.map((item) => {

                            if (item.tags && String(item.tags).toLocaleLowerCase().includes(String(checkI).toLocaleLowerCase())) {
                                id.push(item.id)
                                spostId.push(item.spostId)
                                postTypes.push(item.type)
                            }
                        })
                        isExists = true;
                    }
                })
            }


            var updateSubmitTags = [];
            if (submitTags && submitTags.length > 0) {
                submitTags.map((sTags) => updateSubmitTags.push(capitalizeFirstLetter(sTags)))
            }
            if (id.length>0) {
                let saveObj = {
                    tags: updateSubmitTags.join(),
                    postId: id,
                    wallId: String(wallId),
                    db_table: moderation.filter.db_table,
                    ownerId: localStorage.getItem('ownerId')
                };
                this.props.onDeleteTags(saveObj, () => this.setState({ isDeleteRequest: true }), () => {
                    this.setState({ isDeleteRequest: false })
                    this.props.clickToCloseAddTag()
                });
            }
            else {
                toast.error(`Tag doesn't exist.`);
            }

        }
        else {
            toast.error('Please enter tags.');
        }
    }
    onLoad = () => {
        const { searchPost } = this.state;
        if (searchPost.length > 0) {
            this.setState({ isLoading: true })
            autoCompleteSearch(searchPost).then((response) => {
                const { responseData, responseCode } = response.data;
                if (responseCode === 200) {
                    this.setState({ searchData: responseData?.tags || [], isLoading: false })
                } else {
                    this.setState({ searchData: [], isLoading: false })
                }
            })
        }
        else {
            this.setState({ searchData: [], isLoading: false })
        }
    }
    onSearchText = () => {
        let textInput = document.getElementById('searchTagpost');
        let timeout = null;
        textInput.addEventListener('input', (e) => {
            // textInput.value = e.target.value.trim()
            clearTimeout(timeout);
            timeout = setTimeout(() => {
                this.setState({ searchPost: textInput.value }, () => this.onLoad())
            }, 1000);
        });
    }
    onClickToSearch = (selectSearch) => event => {
        let textInput = document.getElementById('searchTagpost');
        textInput.value = selectSearch;
        this.setState({ searchPost: selectSearch, selected: selectSearch, searchData: [] })
    }
    onClickToAddTags = event => {
        let { submitTags, totalCountTag } = this.state;
        let searchTagpost = document.getElementById('searchTagpost')
        let updateTag = String(searchTagpost.value).toLocaleLowerCase();
        if (!checkIfStringHasSpecialChar(updateTag)) {
            if (checkWordCount(updateTag)) {
                if (!submitTags.includes(updateTag)) {
                    submitTags.push(updateTag)
                    let textInput = document.getElementById('searchTagpost');
                    textInput.value = "";
                    totalCountTag = parseInt(totalCountTag) + 1;
                    this.setState({ selected: "", searchData: [], addTags: true, searchPost: "", submitTags, totalCountTag })
                }
                else {
                    toast.error('Tag already exists.');
                }
            }
            else {
                toast.error('Please enter minimum 3 characters.');
            }
        }
        else {
            toast.error('Special character not allowed.');
        }


    }
    onClickToRemoveTags = (index, tag) => event => {
        let { submitTags, removeTag, submitTagsNew, tagRemoveAction } = this.state;
        submitTags.splice(index, 1)
        tagRemoveAction = parseInt(tagRemoveAction) + 1;

        removeTag.push(tag)
        let updatedTag = submitTagsNew.filter(v => v.toLocaleLowerCase() !== tag.toLocaleLowerCase())
        this.setState({ submitTags, submitTagsNew: updatedTag, });

        if (!submitTags.length) {
            this.setState({ addTags: false })
        }
    }

    render() {
        const { open, clickToCloseAddTag, tags, data,wallId } = this.props
        const { searchData, submitTags, isLoading, searchPost, isRequest, isDeleteRequest } = this.state;
        return (
            <Modal show={open}
                onHide={clickToCloseAddTag}
                backdrop="static"
                keyboard={false}
                centered
                size={"lg"}>
                <Modal.Header closeButton> <h5 className="m-0">Add Tags</h5></Modal.Header>
                <Modal.Body>
                    <div className="row">

                        <div className="col-lg-12 col-12" id="multipleTagSection">
                            <div className="tags-form-main mt-lg-0 mt-5">
                                <div className="form-group d-flex">

                                    <div className="w-100 ">

                                        <div className={`position-relative spinner-right formtagarea ${isLoading ? `spinner` : ``}`}>
                                            <input type="text" className="form-control postTagFilter" id="searchTagpost" autocomplete="off" placeholder="Enter your tag." />

                                            <div className="autoSuggestionList postTagsSugg">
                                                {
                                                    (searchData && searchData.length > 0) ? <div className="dropdown-menu-list">
                                                        <ul className="list-unstyled navi navi-hover">
                                                            {
                                                                searchData.map((sItem, indexItem) => <li key={indexItem} tags={sItem} data-id={sItem} className="image-tags navi-item" onClick={this.onClickToSearch(sItem)}>
                                                                    <a className="navi-link">{sItem}</a>
                                                                </li>)
                                                            }

                                                        </ul>
                                                    </div> : null
                                                }

                                            </div>
                                        </div>
                                    </div>
                                    <div className="ms-3">
                                        <Button variant='primary' title='Add Tag' type="button" className={`btn-add addTagsPost ${String(searchPost).length > 0 ? '' : 'disabled'}`} onClick={this.onClickToAddTags}> <FontAwesomeIcon type={'regular'} icon={'plus'} /></Button>
                                    </div>

                                </div>
                                <div className="showTagsNote" style={submitTags && submitTags.length > 0 ? { display: 'none' } : {}}>
                                    <ul>
                                        <li>Add tags using keywords relevant to the content and click on the + Button.</li>
                                        <li>Tags will help you improve content filtering and ease moderation process.</li>
                                    </ul>
                                </div>
                                <div className="tags-list">
                                    <ul className="postTags">

                                        {
                                            submitTags && submitTags.length > 0 && submitTags.map((tag, tagKey) => {
                                                return (tag && tag.length > 0) ? <li className={tagKey}><span>{capitalizeFirstLetter(tag)}</span><a className="removeSelectedTag" href="javascript:void(0)" alt={tagKey} wallId={wallId} postId={data[tagKey].postId} removeTag={tags} onClick={this.onClickToRemoveTags(tagKey, tag)}> <FontAwesomeIcon type={'regular'} icon={'close'} /></a></li> : null
                                            })
                                        }

                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant='secondary' className={`deleteAllPost px-5 ${isDeleteRequest ? `spinner spinner-white spinner-right disabled` : ``}`} onClick={this.onClickToDelete}>Delete All</Button>
                    <Button variant='primary' type="submit" className={`savePost px-5 ${isRequest ? `spinner spinner-white spinner-right disabled` : ``}`} onClick={this.onClickToSave}>Save</Button>
                </Modal.Footer>
            </Modal>
        )

    }
}

export default AddTag