import { Nav, Badge, Container, Dropdown, NavDropdown } from 'react-bootstrap'
import Filters from './Elements'
import ModSubHeader from './Elements/ModSubHeader'
import { useEffect, useState } from 'react'
import { FontAwesomeIcon } from '../../../../CommonComponents/AssetsHelper'
import { Link } from 'react-router-dom'

const ModHeader = (props) => {
    const { moderation , clickToTab } = props
    const isLoaded = moderation.details && Object.keys(moderation.details).length > 0 ? true : false
    var {activeTab, totalpost, publicpost, privatepost , tabName, TabTotal } = 0
    var upcomingpost = []
    if(isLoaded){
        activeTab = props.activeTab
        totalpost = moderation.details.totalpost
        publicpost = moderation.details.publicpost
        privatepost = moderation.details.privatepost
        upcomingpost = moderation.details.upcomingpost
        tabName = activeTab == 0 ? "All Posts" : activeTab == 1 ? "Public" : activeTab == 2 ? "Private" : "Upcoming" 
        TabTotal = activeTab == 0 ? totalpost : activeTab == 1 ? publicpost : activeTab == 2 ? privatepost : upcomingpost 
    }

    return <div className="dualheader moderation_subheader m-0 position-sticky bg-white end-0 border-top-0 top-0">
                 <Container className="f-between flex-wrap border-bottom border-gray ps-0" fluid>
                    <div className="d-flex align-items-center flex-wrap h-48px">
                        <Link to={`/wall/index/${localStorage.getItem('wallId')}`}
                            className="btn me-3 border-end border-0 py-3 rounded-0">
                            <FontAwesomeIcon icon={'chevron-left p-0'} />
                        </Link>
                        <Nav variant="underline" className='d-none d-lg-flex'>
                            <Nav.Item>
                                <Nav.Link className={`py-3 position-relative border-0 me-3 ${props.activeTab ==0 ? 'active' :''}`} eventKey="allPost" onClick={isLoaded ? clickToTab(0) : null} disabled={!isLoaded}>
                                    All Posts
                                    <Badge bg="" className={`m-0 py-1 ms-2 ${activeTab == 0 ? "badge-gradient":"badge-light-primary"}`}>{totalpost}</Badge> 
                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link className={`py-3 position-relative border-0 me-3 ${props.activeTab ==1 ? 'active' :''}`} eventKey="public" onClick={isLoaded ? clickToTab(1) : null} disabled={!isLoaded}>
                                    Public
                                    <Badge bg="" className={`m-0 py-1 ms-2 ${activeTab == 1 ? "badge-gradient":"badge-light-primary"}`}>{publicpost}</Badge> 
                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link className={`py-3 position-relative border-0 me-3 ${props.activeTab ==2 ? 'active' :''}`} eventKey="private" onClick={isLoaded ? clickToTab(2) : null} disabled={!isLoaded}>
                                    Private
                                    <Badge bg="" className={`m-0 py-1 ms-2 ${activeTab == 2 ? "badge-gradient":"badge-light-primary"}`}>{privatepost}</Badge> 
                                </Nav.Link>
                            </Nav.Item>
                            {upcomingpost && upcomingpost >= 0 ? <Nav.Item>
                                <Nav.Link className={`py-3 position-relative border-0 me-3 ${props.activeTab ==3 ? 'active' :''}`} eventKey="upcomming" onClick={isLoaded ? clickToTab(3) : null} disabled={!isLoaded}>
                                    Upcoming
                                    <Badge bg="" className={`m-0 py-1 ms-2 ${activeTab == 3 ? "badge-gradient":"badge-light-primary"}`}>{upcomingpost}</Badge> 
                                </Nav.Link>
                            </Nav.Item>:null}
                        </Nav>
                        <Dropdown className='d-flex d-lg-none'>
                            <Dropdown.Toggle variant="secondary" size='sm' id="mod-tab">
                            {tabName} <Badge bg="" className={"m-0 py-1 badge-light-primary mx-1"}>{TabTotal}</Badge>
                            </Dropdown.Toggle>
                            <Dropdown.Menu>
                                <NavDropdown.Item eventKey="1" onClick={isLoaded ? clickToTab(0) : null} >All Posts <Badge className='ms-2 rounded-3' bg={"light-primary"}>{totalpost}</Badge> </NavDropdown.Item>
                                <NavDropdown.Item eventKey="2" onClick={isLoaded ? clickToTab(1) : null} >Public <Badge className='ms-2 rounded-3' bg={"light-primary"}>{publicpost}</Badge> </NavDropdown.Item>
                                <NavDropdown.Item eventKey="3" onClick={isLoaded ? clickToTab(2) : null}>Private <Badge className='ms-2 rounded-3' bg={"light-primary"}>{privatepost}</Badge> </NavDropdown.Item>
                                {upcomingpost && upcomingpost > 0 ? <NavDropdown.Item eventKey="4" onClick={isLoaded ? clickToTab(3) : null} >Upcoming <Badge className='ms-2 rounded-3' bg={"light-primary"}>{upcomingpost}</Badge> </NavDropdown.Item>:null}
                            </Dropdown.Menu>
                        </Dropdown>
                    </div>
                    {activeTab != 3 ?  <div className="d-flex align-items-center flex-wrap flex-grow-1 justify-content-end">
                        <Filters {...props} noPost={moderation?.total == 0 && !moderation?.filterLoader}/>
                    </div> : null}
                </Container>
                {activeTab != 3 && (!(moderation?.total == 0 && !moderation?.filterLoader)) && <ModSubHeader {...props} />}
            </div>
}
export default ModHeader;

