import React, { useEffect, useState } from 'react'
import QuickSearch from './QuickSearch'
import DateRange from './dateRange'
import { Button, Form } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../CommonComponents/AssetsHelper';
import withRouter from '../../../../../CommonComponents/WithRouter';

const Filters = (props) => {
    const { filterTab,  changeFilterTab , allWalls, params, noPost } = props;
    const [isCarosal, setisCarosal] = useState()  

    useEffect(() => {
        const foundItem = allWalls?.length && allWalls.find(item => item.id == params.id);
        setisCarosal(foundItem?.carouselPost || 0);
    }, [props]);

    return <div className="d-flex align-items-center flex-wrap">
        <QuickSearch {...props} />
        <DateRange {...props}>
            <Button variant="link" size='sm' className="btn-icon border-0 ms-2 f-center" id="calander_" onClick={() => changeFilterTab(filterTab === 2 ? 0 : 2)}>
                <FontAwesomeIcon icon={'calendar-range'} />
            </Button>
        </DateRange>
        <Button variant="link" size='sm' className="btn-icon border-0 ms-2 f-center" id="filter_aside" onClick={() => changeFilterTab(filterTab === 3 ? 0 : 3)}>
            <FontAwesomeIcon icon={'filter'} />
        </Button>

    </div>
}

export default withRouter(Filters);