import React from 'react'
import { FontAwesomeIcon } from '../../../../CommonComponents/AssetsHelper';
import { Button, Pagination } from 'react-bootstrap';

const ModerationPagination = ({ moderation, onNextNavigateClick, onPrevNavigateClick , onChangePage}) => {
    const { paginateData } = moderation;
    if (!paginateData || !Object.keys(paginateData).length) return null;
    
    return <Pagination size='sm' className='ms-3 mb-0'> 
            <Pagination.Prev className={`page-arrow page-item ${paginateData.currentPage == 1 ? ' disabled' : ' cursor-pointer'}`} onClick={paginateData.currentPage == 1 ? null : onPrevNavigateClick} />
            {/* <li className="page-item">
                <span className={`page-link ${paginateData.currentPage == 1 ? ' disabled' : ' cursor-pointer'}`} onClick={paginateData.currentPage == 1 ? null : onPrevNavigateClick}>
                    <FontAwesomeIcon type={'solid'} icon={'angle-left'} />
                </span>
            </li> */}
            {(paginateData.pages && paginateData.pages.length > 0) ? paginateData.pages.map((item, pagesIndex) => {

                return (parseInt(pagesIndex) < 1) ?
                    <Pagination.Item key={`lIi${pagesIndex}`} className={` page-item${paginateData.currentPage == item ? " active" : ' cursor-pointer'}`} onClick={onChangePage(item)}>{item}</Pagination.Item> :

                    (((paginateData.currentPage <= 3) && (parseInt(pagesIndex) === 2 || parseInt(pagesIndex) === 1)) || (paginateData.currentPage >= parseInt(paginateData.pages.length) - 2 && (parseInt(pagesIndex) >= parseInt(paginateData.pages.length) - 3))) ?
                    <>
                        {pagesIndex === parseInt(paginateData.pages.length) - 3 && parseInt(paginateData.pages.length) > 4 ?
                            <Pagination.Ellipsis key={`li${pagesIndex}`} className="page-item disabled" /> : null
                        }
                        <Pagination.Item key={`li${pagesIndex}`} className={`page-item${paginateData.currentPage == item ? " active" : ' cursor-pointer'}`} onClick={onChangePage(item)}>{item}</Pagination.Item>
                    </>
                    :
                    (parseInt(paginateData.pages.length) - 3 == pagesIndex) ?
                    (paginateData.currentPage > 3 && paginateData.currentPage < paginateData.pages.length - 2) ?
                        <>
                            <Pagination.Ellipsis key={`li${pagesIndex}`} className="page-item disabled" />

                            <Pagination.Item key={`li${pagesIndex}`} className={`page-item active`} onClick={onChangePage(item)}>{paginateData.currentPage}</Pagination.Item>

                            <Pagination.Ellipsis key={`li${pagesIndex}`} className="page-item disabled" />
                        </>
                        :
                        <Pagination.Ellipsis key={`li${pagesIndex}`} className="page-item disabled" />

                    : (parseInt(paginateData.pages.length) - 1 == pagesIndex) ?

                    <Pagination.Item key={`li${pagesIndex}`} className={`page-item ${paginateData.currentPage == item ? " active" : ' cursor-pointer'}`} onClick={onChangePage(item)}>{item}</Pagination.Item>
                    : null
            }) : null}
            
            <Pagination.Next className={`page-arrow page-item ${paginateData.currentPage == paginateData.totalPages ? ' disabled' : ' cursor-pointer'}`} onClick={paginateData.currentPage == paginateData.totalPages ? null : onNextNavigateClick}/>

        </Pagination>
}
export default ModerationPagination;
